import { Location } from '@angular/common';
import { Component } from '@angular/core';

@Component({
    selector: 'app-back-button',
    template: `<a (click)="goBack()" class="btn btn-default btn-sm"><i class="fas fa-chevron-left"></i> Back</a> | `,
})
export class EsBackButtonComponent {
    constructor(private location: Location) { }

    goBack() {
        this.location.back();
    }
}
