import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './services/index';

//  Import Containers
import {
    FullLayoutComponent,
    SimpleLayoutComponent
} from './containers';

export const routes: Routes = [
    {
        path: '',
        redirectTo: 'dashboard',
        pathMatch: 'full',
        canActivate: [AuthGuard]
    },
    {
        path: '',
        component: FullLayoutComponent,
        data: {
            title: 'Home'
        },
        children: [
            
            {
                path: 'dashboard',
                loadChildren: './views/dashboard/dashboard.module#DashboardModule'
            },
            {
                path: 'member',
                loadChildren: './views/member/member.module#MemberModule'
            },
            {
                path: 'customer',
                loadChildren: './views/customer/customer.module#CustomerModule'
            },
            {
                path: 'training',
                loadChildren: './views/training/training.module#TrainingModule'
            },
            {
                path: 'transaction',
                loadChildren: './views/transaction/transaction.module#TransactionModule'
            },
            { 
                path: 'laporan',
                loadChildren: './views/laporan/laporan.module#LaporanModule'
            },
        ],
        canActivate: [AuthGuard]
    },
    {
        path: 'pages',
        component: SimpleLayoutComponent,
        data: {
            title: 'Pages'
        },
        children: [
            {
                path: '',
                loadChildren: './views/pages/pages.module#PagesModule',
            }
        ]
    },
    {
        path: 'system',
        component: FullLayoutComponent,
        data: {
            title: 'System'
        },
        children: [
            {
                path: '',
                loadChildren: './views/system/system.module#SystemModule',
            }
        ],
        canActivate: [AuthGuard]
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
