import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from '../../../environments/environment';

export abstract class BaseService {

    protected endPoint;
    protected env;

    constructor(private http: HttpClient) {
        this.env = environment;
    }

    get(filters) {

        let httpParams = new HttpParams();
        Object.keys(filters).forEach(function (key) {
             httpParams = httpParams.append(key, filters[key]);
        });

        return this.http.get(
            this.env.appUrl + '/' + this.endPoint, {params: httpParams}
        );
    }

    save(request) {
        return this.http.post(
            this.env.appUrl + '/' + this.endPoint,
            request
        );
    }

    update(request, primaryKey) {
        return this.http.put(
            this.env.appUrl + '/'  + this.endPoint + '/' + primaryKey,
            request,
        );
    }

    delete(primaryKey) {
        return this.http.delete(
            this.env.appUrl + '/' + this.endPoint + '/' + primaryKey,
        );
    }

    getDetail(primaryKey) {
        return this.http.get(
            this.env.appUrl + '/' + this.endPoint + '/detail/' + primaryKey
        );
    }

}
