import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { environment } from '../../environments/environment';

@Injectable()
export class UsersService {
    constructor(private http: HttpClient) { }

    get(filters) {

        let httpParams = new HttpParams();
        Object.keys(filters).forEach(function (key) {
             httpParams = httpParams.append(key, filters[key]);
        });

        return this.http.get(
            environment.appUrl + '/users', {params: httpParams}
        );
    }

    save(request) {
        return this.http.post(
            environment.appUrl + '/users',
            request
        );
    }

    update(request, primaryKey) {
        return this.http.put(
            environment.appUrl + '/users/' + primaryKey,
            request,
        );
    }

    delete(primaryKey) {
        return this.http.delete(
            environment.appUrl + '/users/' + primaryKey,
        );
    }

    password(request, primaryKey) {
        return this.http.put(
            environment.appUrl + '/users/password/' + primaryKey,
            request,
        );
    }

}
